.shop-nav {
  display: flex;
   align-items: center;
  // margin-left: 100px;

  &__item{
    &:not(:last-child){
      margin-right: 25px;
    }
  }
  &__link{
    display: inline-block;
    width: 16px;
    height: 16px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &--user {
      background-image: url(../../img/user.png);
    }
  }
}
.cart {
  width: auto;
  height: auto;

  &__icon{
    display: inline-block;
    width: 16px;
    height: 16px;
    background-image: url(../../img/bag.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

  }
  &__q{
    display: inline-flex;
    align-items: center;
    border-radius: 25px;
    padding: 3px 5px;
    font-weight: 700;
    font-size: 10px;
    height: 16px;
    color: bisque;
    background-color: #000;
  }
}
.search-form{
  &__btn{
     display: inline-block;
    width: 16px;
    height: 16px;
    background-image: url(../../img/search.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
